import React, { useState } from "react";
import { useForm } from "react-hook-form";
// import { Redirect } from 'react-router';
import './App.css';


const apiUrl = "http://localhost:4500/api/v1";
// const apiUrl = "https://stably.herokuapp.com/api/v1/";

const App = () =>  {
  const { register, handleSubmit, handleChange } = useForm();
  const [ data, setData ] = useState(null);
  const  [loading, setLoading] = useState(false);

  const getPrimeNumber = (data) => {    
    setLoading(true);
    fetch(`${apiUrl}/get-highest-prime-number`, {
      method: "post",
      headers: {
            'Accept': 'application/json',
            'Content-type': 'application/json'
        },
      body: JSON.stringify({
        "number": data.number
      })
    })
    .then(resp => resp.json())
    .then(
        response => {
            setData(response.data)
            setLoading(false)
        }, 
        err => {
            console.log(err);
        }
    )
  }
  

  return (
    <div className="App">
      <form onSubmit={handleSubmit(getPrimeNumber)}>
          <input {...register('number')} type="number" name="number" placeholder="Enter number here" onChange={handleChange} min="0" required/>
          <button className={loading ? "loading":""}> {loading ? "":"Get Value"}  </button>
         </form>
         <p>
            {data ? `The highest prime number less than the input is: ${data}` : null}
         </p>
    </div>
  );
}

export default App;
